.arrow__inner {
  display: inline-block;
  width: 2.8125rem;
  height: 2.8125rem;
  margin-right: 0rem;
  background-color: #fff;
  float: right;
  overflow: hidden;
  position: relative;
}
.arrow__inner::before {
  content: "";
  display: inline-block;
  width: 100%;
  height: 100%;
  background-color: #333;
  position: absolute;
  top: 0;
  left: -100%;
  transition: 0.7s;
}

.arrow__inner:hover::before {
  left: 0;
  transition: 0.7s;
}

.arrow__line {
  display: inline-block;
  width: 100%;
  height: 100%;
  text-align: center;
  position: relative;
}

.arrow__line::before {
  content: "";
  display: inline-block;
  width: 1.1875rem;
  height: 0.125rem;
  background-color: #333;
  transform: rotate(45deg);
  position: absolute;
  top: 1.375rem;
  left: 1.1875rem;
}

.arrow__line::after {
  content: "";
  display: inline-block;
  width: 1.1875rem;
  height: 0.125rem;
  background-color: #333;
  transform: rotate(-45deg);
  position: absolute;
  top: 1.375rem;
  left: 0.4375rem;
}

.arrow__line:hover::before,
.arrow__line:hover::after {
  background-color: #fff;
  z-index: 99;
}

