@use "global" as *;
.header__li {
  // size
  display: flex;
  // ajustment
  justify-content: center;
  // font
  font-family: "Noto Sans JP", sans-serif;
  font-size: rem(16);
  font-weight: $normal;
  font-style: normal;
  letter-spacing: rem(2);
  line-height: 2.1875;
  // decoration
  color: #fff;
  // space
  margin-bottom: rem(20);

  @include mq(md) {
    // ajustment
    display: flex;
    align-items: center;
    // space
    margin-right: rem(29.6);
    // ajustment -> `.header__li--link-a::before`
    position: relative;
  }
  @include mq(xl) {
  }
}

.header__li:last-child {
  @include mq(md) {
    // space
    margin-right: rem(0);
  }
  @include mq(xl) {
  }
}

.header__li--link-a {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__li--link-a:hover::before {
  @include mq(md) {
    // decoration
    opacity: 1;
  }
  @include mq(xl) {
  }
}

.header__li--link-a::before {
  @include mq(md) {
    content: "";
    // size
    display: block;
    width: 100%;
    height: 2px;
    // decoration
    background-color: #ffea2e;
    opacity: 0;
    // ajustment -> `.header__li`
    position: absolute;
    bottom: rem(10);
    left: 0;
    transition: 0.7s;
  }
  @include mq(xl) {
  }
}

